const CookieBtn = () => {
	const closeBtn = document.querySelector('.cookieClose');
	const cookieSection = document.querySelector('.cookie');
	const acceptBtn = document.querySelector('.cookieAccept');
	const cookieAcceptMob = document.querySelector('.cookieAcceptMob');
	const body = document.querySelector('body');

	if (cookieSection) {
		closeBtn.addEventListener('click', () => {
			cookieSection.classList.add('cookie--close_mod');
		});

		acceptBtn.addEventListener('click', () => {
			cookieSection.classList.add('cookie--accept_mod');
		});

		cookieAcceptMob.addEventListener('click', () => {
			body.classList.add('body--mobile_cookie');
			cookieSection.classList.add('cookie--accept_mod');
		});
	}
};

export default CookieBtn;
