// ------------------- imports
import { pageLoad, onWindowResize } from 'utils';
// ------------------- imports###

// ------------------  import layout
// import buttonToOpenCalendly from '../components/buttonToOpenCalendly';
import Header from './Header';
import Footer from './Footer';
import Cookie from '../components/Cookie';
// import 'swiper/css';
// import 'ScssComponents/_hero.scss';
// import 'ScssComponents/_team_hero.scss';
// import 'ScssComponents/_showcase_hero.scss';
// import 'ScssComponents/_showcases_list.scss';
// @import '../components/not_found';
import { calcViewportHeight } from '../utils';
// ------------------  import layout###

// ------------------  import styles
// ------------------  import styles###
export default class Layout {
	constructor() {
		this.$body = document.body;
		this.init = this.init.bind(this);
		this.init();
	}

	loadFunc() {
		calcViewportHeight();
		Header();
		Footer();
		// buttonToOpenCalendly();
		Cookie();
	}

	init() {
		pageLoad(() => {
			this.loadFunc();
		});

		onWindowResize(() => {
			calcViewportHeight();
		});
	}
}
